import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { TreeView, TreeNode } from "carbon-components-react";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-contenttree items=[{name: "Node1", label: "Node 1", children: [{name: "Node11", label: "Node 11"}]}, {name: "Node2", label: "Node 2", children: [{name: "Node21", label: "Node 21"}]}]/>
*/

const SrsContentTree = function (props, ref) {
     
    const [expandedNodes, setExpandedNodes] = React.useState(props.selectedpath?.split(".") || []);

    const handleToggle = (name) => {
      if (expandedNodes.includes(name)) {
        setExpandedNodes(expandedNodes.filter((node) => node !== name));
      } else {
        setExpandedNodes([...expandedNodes, name]);
      }
    };

    const treeRef = useRef(null);
    useEffect(() => {
        if (treeRef.current) {
            treeRef.current.addEventListener(EventAPI.ICON_CLICKED, (evt) => {
                console.log(evt.detail.id);
            })
        }
    }, [props]);

    const renderTreeNodes = (items, parent) => {
        const nodes = [];
        if(items && items.length > 0) {
            items.forEach((item, index) => {
                let nodesExpanded = props.selectedpath?.split(".") || expandedNodes;
                const isExpanded = nodesExpanded.includes(item.name);
                nodes.push(<TreeNode isExpanded={isExpanded} data-testid={item.name} label={item.label} 
                                onClick={(evt) => {
                                    props.dispatchEvent(EventAPI.CONTENTTREE_NODE_CLICKED, { payload: {id: props.id || props.elementid, nodeid: item.name, nodepath: parent}});
                                }} 
                                onToggle={(evt) => {
                                    props.dispatchEvent(EventAPI.CONTENTTREE_NODE_CLICKED, { payload: {id: props.id || props.elementid, nodeid: item.name, nodepath: parent}});
                                }} 
                                renderIcon={() => {
                                    return (<div style={{ display: "flex", flexDirection: "row", padding: "0px 10px"}}>
                                    {
                                        props.actionicons.map((actionIcon) => {
                                            return (<mui-icon-button icon={actionIcon} size="16" style={{cursor: "pointer"}} elementid={item.name}/>)
                                        })
                                    }
                                </div>)
                            }}>
                        {item.children && item.children.length > 0? renderTreeNodes(item.children, parent + "." + item.name): null}
                    </TreeNode>);
                }
            );
        }
    
        return nodes;
    };

    return <div ref={treeRef}>
        <TreeView>
            {renderTreeNodes(props.items, "")}
        </TreeView></div>;
}

const styles = `
    .cds--tree {
        width: 100% !important;
    }
`
const props = [{
    name: "items",
    defaultValue: [],
    type: "object"
}, {
    name: "actionicons",
    defaultValue: ["Add"],
    type: "object"
}, {
    name: "selectedpath",
    defaultValue: "",
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsContentTree), props, "", ["mui-icon-button"]);
export { WrappedComponent, styles, props, dispatchedEvents, slots };